<template>
    <div>
        <div class="card">
            <div class="card-body">
                <div class="alert alert-danger" v-if="errmsg">{{ errmsg }}</div>
                <div class="form-group">
                    <label for="email" class="form-label">Email:</label>
                    <input type="email" name="email" id="email" class="form-control" v-model="email">
                    <label for="password" class="form-label">Password:</label>
                    <input type="password" name="password" id="password" v-model="password" class="form-control">
                    <button type="button" class="btn btn-success" @click="login">Sign in</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref} from 'vue';
import {getAuth,signInWithEmailAndPassword} from'firebase/auth'
import { useRouter } from 'vue-router';
import Swal from 'sweetalert2'
const router = useRouter();

const email = ref('');
const password = ref('');
const errmsg = ref('')


const login = async()=>{
    try {
    const userCredential = await signInWithEmailAndPassword(getAuth(),email.value, password.value);
    if(userCredential){
        Swal.fire('Success','Log in Successfully','success')
        router.push('/admin/adminView')
    }
  } catch (error) {
    console.error('User creation error:', error.message);
    Swal.fire('Error','Log in Failed','error')
  }
}

</script>

