<script setup>
import {db} from '@/firebase/index.js'
import { ref } from 'vue';
import {addDoc,collection} from 'firebase/firestore';
import Swal from 'sweetalert2'

const fullname = ref('');
const email = ref('')
const message = ref('')

const collectionRef = collection(db,'contacts');


const createMessage = async()=>{
    const dataObj = {
        fullname:fullname.value,
        email:email.value,
        message:message.value
    }
    try {
        const docRef = await addDoc(collectionRef,dataObj);
        if(docRef){
            Swal.fire('Success','Message has been Sent','success')
            fullname.value = ''
            email.value = ''
            message.value = ''
        }
    } catch (error) {
        console.log(error);
        Swal.fire('Error','Message is not sent','error')
    }
}

</script>

